import * as React from "react";
import styled from "styled-components";
import { motion } from "framer-motion";

import PaperCard from "../ui/PaperCard";
import CardEffectsText from "../ui/CardEffectsText";
import { cardIsPlayable } from "../../game/utils";

interface HandStageProps {
  playerFightState: PlayerFightState;
  playCard: (card: Card) => void;
}
const HandStage: React.FC<HandStageProps> = ({
  playerFightState,
  playCard,
}) => {
  const { hand } = playerFightState.cards;
  return (
    <HandStageWrapper>
      {hand.map((card, i) => {
        // TODO: Figure out why I need `0.5` (I just added it, and it maginally works).
        const normalizedIndex = i - hand.length + hand.length / 2 + 0.5;
        const isPlayable = cardIsPlayable(playerFightState, card);
        return (
          <AnimatedCardWrapper
            key={card.id}
            layoutId={`${card.id}`}
            drag
            dragConstraints={{ top: 0, left: 0, right: 0, bottom: 0 }}
            dragElastic={1}
            initial={{
              backgroundColor: "#FFFFFF",
            }}
            animate={{
              scale: 1,
              rotate: normalizedIndex * 10,
              y: Math.abs(normalizedIndex) * (Math.abs(normalizedIndex) * 20),
              backgroundColor: "#FFFFFF",
            }}
            whileHover={{ scale: 1.15, rotate: 0 }}
            onDragEnd={(_event, pathInfo) => {
              // TODO: Make this zone detection better
              if (pathInfo.offset.y <= -200 && isPlayable) {
                playCard(card);
              }
            }}
          >
            <PaperCard card={card} disabled={!isPlayable} onClick={() => null}>
              <CardEffectsText card={card} />
            </PaperCard>
          </AnimatedCardWrapper>
        );
      })}
    </HandStageWrapper>
  );
};

const HandStageWrapper = styled.div`
  display: flex;
  justify-content: center;
  flex: 0;
  position: relative;
  margin-left: 16px;
`;

const AnimatedCardWrapper = styled(motion.div)`
  margin-right: -20px;
  border-radius: 8px;
  z-index: 0;

  &:hover {
    z-index: 1;
  }
`;

export default HandStage;
