import { sample, shuffle } from "lodash";

import Model from "~/logic/model";

// TODO: Move all the 'create new player' stuff elsewhere

import * as fightHooks from "~/game/hooks";

const createNewPlayer = (deck: Card[]): Player => {
  return {
    deck,
    energy: 3,
    baseAttributes: {
      hp: 100,
      certainty: 100,
    },
  };
};

const createDescartes = (): Enemy => {
  return {
    name: "Descartes",
    baseAttributes: {
      hp: 100,
      certainty: 100,
    },
    hooks: [fightHooks.doubt(), fightHooks.insight(), fightHooks.cogito()],
  };
};

export const initFight = (deck: Card[]) => {
  console.log("Init!");

  const player = createNewPlayer(deck);
  const enemy = createDescartes();

  return new Model<FightState, FightActions, FightEffects>({
    state: {
      turn: 0,
      player: {
        player,
        energy: 3,
        attributes: {
          hp: 100,
          certainty: 0,
        },
        hooks: [fightHooks.blankSlate("player")],
        cards: {
          hand: [],
          draw: shuffle(player.deck),
          discard: [],
        },
      },
      enemy: {
        enemy,
        attributes: {
          hp: 100,
          certainty: 0,
        },
        hooks: [fightHooks.blankSlate("enemy"), ...enemy.hooks],
      },
    },
    actions: {
      INCREMENT_TURN: (state) => {
        return {
          ...state,
          turn: state.turn + 1,
        };
      },

      DRAW_CARD: (state) => {
        const newState = { ...state };
        const card = state.player.cards.draw.pop();
        if (card) {
          newState.player.cards.hand = [...newState.player.cards.hand, card];
          return newState;
        } else {
          return false;
        }
      },

      DISCARD_CARD: (state, { card }) => {
        const newState = { ...state };
        newState.player.cards.hand = state.player.cards.hand.filter(
          (cardInHand) => card.id !== cardInHand.id
        );
        newState.player.cards.discard = [...state.player.cards.discard, card];
        return newState;
      },

      SHUFFLE_CARDS: (state) => {
        const newState = { ...state };

        newState.player.cards.draw = [...shuffle(state.player.cards.discard)];
        newState.player.cards.discard = [];

        return newState;
      },

      SET_ENERGY: (state, { energy }) => {
        return {
          ...state,
          player: { ...state.player, energy },
        };
      },

      SET_ATTRIBUTE: (state, { target, attribute, value }) => {
        return {
          ...state,
          [target]: {
            ...state[target],
            attributes: {
              ...state[target].attributes,
              [attribute]: value,
            },
          },
        };
      },
    },

    effects: {
      START_FIGHT: async ({ affect }) => {
        await affect("START_TURN", {});
      },

      START_TURN: async ({ dispatch, affect, state }) => {
        await dispatch("INCREMENT_TURN", {});
        await dispatch("SET_ENERGY", {
          energy: state.player.player.energy,
        });

        await affect("RUN_HOOKS", {
          hooks: state.player.hooks.filter(
            (hook) => hook.when === "start-of-turn"
          ),
        });

        await affect("DRAW_CARDS", { nCards: 5 });
      },

      END_TURN: async ({ dispatch, affect, state }) => {
        for (const card of state.player.cards.hand) {
          await dispatch("DISCARD_CARD", { card });
        }
        await affect("RUN_EMENY_AI", {});
        await affect("START_TURN", {});
      },

      RUN_EMENY_AI: async ({ affect, state }) => {
        await affect("RUN_HOOKS", {
          hooks: state.enemy.hooks.filter(
            (hook) => hook.when === "start-of-turn"
          ),
        });
      },

      RUN_HOOKS: async ({ data: { hooks }, affect, state }) => {
        for (const hook of hooks) {
          const isScripted = state.turn === hook.turn;
          if (hook.turn != undefined && !isScripted) {
            continue;
          }

          const isSuccess = hook.p >= Math.random();
          const shouldRun = isScripted ? isScripted && isSuccess : isSuccess;

          if (shouldRun) {
            for (const effect of hook.effects) {
              await affect(effect.name, effect.data);
            }
          }
        }
      },

      PLAY_CARD: async ({ data: { card }, dispatch, affect, state }) => {
        await dispatch("SET_ENERGY", {
          energy: state.player.energy - card.cost,
        });

        for (const effect of card.effects) {
          await affect(effect.name, effect.data);
        }

        await dispatch("DISCARD_CARD", { card });
      },

      DRAW_CARDS: async ({ data: { nCards = 1 }, dispatch }) => {
        let nCardsDrawn = 0;
        while (nCardsDrawn < nCards) {
          const successState = await dispatch("DRAW_CARD", {});
          if (successState) {
            nCardsDrawn++;
          } else {
            await dispatch("SHUFFLE_CARDS", { kind: "discard-to-draw" });
          }
        }
      },

      DEAL_DAMAGE: async ({ data: { target, damage }, state, dispatch }) => {
        const targetCertainty = state[target].attributes.certainty;
        const unblockedDamage = damage - targetCertainty;

        if (targetCertainty > 0) {
          await dispatch("SET_ATTRIBUTE", {
            target,
            value: Math.max(state[target].attributes.certainty - damage, 0),
            attribute: "certainty",
          });
        }

        if (unblockedDamage >= 0) {
          await dispatch("SET_ATTRIBUTE", {
            target,
            value: state[target].attributes.hp - unblockedDamage,
            attribute: "hp",
          });
        }
      },

      GAIN_CERTAINTY: async ({
        data: { target, certainty },
        state,
        dispatch,
      }) => {
        await dispatch("SET_ATTRIBUTE", {
          target,
          value: state[target].attributes.certainty + certainty,
          attribute: "certainty",
        });
      },

      CLEAR_CERTAINTY: async ({ data: { target }, dispatch }) => {
        await dispatch("SET_ATTRIBUTE", {
          target,
          value: 0,
          attribute: "certainty",
        });
      },
    },
  });
};
