import * as React from "react";
import styled from "styled-components";
import { AnimateSharedLayout } from "framer-motion";

import { useModel, createModelContext } from "~/logic/ModelProvider";
import { initFight } from "~/game/fight";
import { startingDeck } from "~/game/cards";

import FightStageHeader from "./FightStageHeader";
import FightStageFooter from "./FightStageFooter";
import PlayerStage from "./PlayerStage";
import EnemyStage from "./EnemyStage";
import HandStage from "./HandStage";
import VictoryModal from "./VictoryModal";
import DefeatModal from "./DefeatModal";

const fight = initFight(startingDeck());

const FightStage: React.FC = () => {
  const [state, { affect, run }] = useModel(fight);
  React.useEffect(() => {
    affect("START_FIGHT", {});
    run();
  }, []);

  const victory = state.enemy.attributes.hp <= 0
  const defeat = state.player.attributes.hp <= 0

  const playCard = (card: Card) => {
    affect("PLAY_CARD", { card });
    run();
  };

  const endTurn = () => {
    affect("END_TURN", {});
    run();
  };

  return (
    <AnimateSharedLayout>
      <FightStageWrapper>
        <FightStageHeaderWrapper>
          <FightStageHeader fightState={state} />
        </FightStageHeaderWrapper>
        <FightStageContentWrapper>
          <PlayerStage playerFightState={state.player} />
          <EnemyStage enemyFightState={state.enemy} />
        </FightStageContentWrapper>
        <HandStage playerFightState={state.player} playCard={playCard} />
        <FightStageFooterWrapper>
          <FightStageFooter fightState={state} endTurn={endTurn} />
        </FightStageFooterWrapper>
        <VictoryModal open={victory} />
        <DefeatModal open={defeat} />
      </FightStageWrapper>
    </AnimateSharedLayout>
  );
};

const FightStageWrapper = styled.div`
  padding: 32px;
  display: flex;
  flex-direction: column;
  height: calc(100vh - 64px);
`;

const FightStageHeaderWrapper = styled.header`
  flex: 0;
`;

const FightStageContentWrapper = styled.div`
  flex: 1;
  display: flex;
  justify-content: space-between;
`;

const FightStageFooterWrapper = styled.div`
  flex: 0;
`;

export default FightStage;
