export const doubt = (): FightStateHook => {
  return {
    p: 0.95,
    when: "start-of-turn",
    effects: [
      {
        name: "DEAL_DAMAGE",
        data: {
          target: "player",
          damage: 10,
        },
      },
    ],
  };
};

