import * as React from "react";
import styled from "styled-components";

import ResourceBars from "./ResourceBars";
import PlayerImage from "~/assets/player.png"

interface PlayerStageProps {
  playerFightState: PlayerFightState;
}

const PlayerStage: React.FC<PlayerStageProps> = ({ playerFightState }) => {
  return (
    <div>
      <h2>Player</h2>
      <StageWrapper>
        <ResourceBars
          hp={{
            value: playerFightState.attributes.hp,
            total: playerFightState.player.baseAttributes.hp,
          }}
          certainty={{
            value: playerFightState.attributes.certainty,
            total: playerFightState.player.baseAttributes.certainty,
          }}
        />
        <PlayerFigure />
      </StageWrapper>
    </div>
  );
};

const StageWrapper = styled.div`
  display: flex;
`;

const PlayerFigure = styled.img.attrs({
  src: PlayerImage,
})`
  height: 200px;
`;

export default PlayerStage;
