import { shuffle } from "lodash";

export const cardIsPlayable = (
  playerFightState: PlayerFightState,
  card: Card
) => {
  return playerFightState.energy >= card.cost;
};

export const drawCards = (
  fightState: FightState,
  nCards: number,
  options?: {
    // TODO: Consider _not_ doing this
    mutate?: boolean;
  }
) => {
  const nextState = options?.mutate ? fightState : { ...fightState };
  let cards: Card[] = [];
  while (cards.length < nCards) {
    const card = nextState.player.drawPile.shift();
    if (card) {
      cards.push(card);
    } else {
      nextState.player.drawPile = shuffle([...nextState.player.discardPile]);
      nextState.player.discardPile = [];
    }
  }
  nextState.player.hand = [...nextState.player.hand, ...cards];
  return nextState;
};
