import { times } from "lodash";

const createCard = (card: Omit<Card, "id">): Card => {
  const id = Math.random() * 100000;
  return { id, ...card };
};

export const insightCard = (): Card => {
  return createCard({
    name: "Insight",
    type: "skill",
    cost: 1,
    effects: [
      {
        name: "GAIN_CERTAINTY",
        data: {
          target: "player",
          certainty: 12,
        },
      },
    ],
  });
};

export const doubtCard = (): Card => {
  return createCard({
    name: "Doubt",
    type: "attack",
    cost: 1,
    effects: [
      {
        name: "DEAL_DAMAGE",
        data: {
          target: "enemy",
          damage: 10,
        },
      },
    ],
  });
};

export const startingDeck = () => {
  return [
    ...times(5, insightCard),
    ...times(5, doubtCard),
  ];
};
