import * as React from "react";
import styled from "styled-components";

function usePrevious<T>(value: T) {
  const ref = React.useRef<T>();
  React.useEffect(() => {
    ref.current = value;
  });
  return ref.current;
}

interface VerticalBarProps {
  label: React.ReactNode;
  value: number;
  total: number;
  color: string;
}

const VerticalBar: React.FC<VerticalBarProps> = ({
  label,
  value,
  total,
  color,
}) => {
  // TODO: Add logic to display "deltas"
  // const previousValue = usePrevious(value);
  // React.useEffect(() => {
  // }, [value]);
  return (
    <VerticalBarWrapper>
      <BarTrack>
        <BarValue value={value} total={total} color={color} />
      </BarTrack>
      <Value>
        {value} / {total}
      </Value>
      <Label>{label}</Label>
    </VerticalBarWrapper>
  );
};

const VerticalBarWrapper = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 8px;
`;

const Label = styled.span`
  display: block;
  font-weight: bold;
  text-align: center;
  margin-top: 8px;
  margin-bottom: 8px;
`;

const Value = styled.span`
  display: block;
  text-align: center;
  font-size: 0.85em;
  margin-top: 4px;
  margin-bottom: 4px;
`;

const BarTrack = styled.div`
  position: relative;
  width: 20px;
  height: 200px;
  background: rgba(255, 255, 255, 0.15);
  /* box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.3); */
  border-radius: 20px;
  overflow: hidden;
`;

const BarValue = styled.div<Omit<VerticalBarProps, "label">>`
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: ${(props) => Math.min((props.value / props.total) * 100, 100)}%;
  background-color: ${(props) => props.color};
  transition: height 150ms ease-out;
`;

export default VerticalBar;
